<template>
	<view class="search">
		<musichead title="搜索" :icon="true" :iconblack="true"></musichead>
		<view class="container" :style="{height: 'calc(100vh - ' + statusBarHeight + 'px - ' + navBarHeight + 'px)' }">
			<scroll-view scroll-y="true">
				<!-- 搜索框 -->
				<view class="search-search">
					<text class="iconfont icon-fangdajing"></text>
					<input type="text" placeholder="搜索歌曲" v-model="searchWord" @confirm="handleToSearch(searchWord)"
						@input="handleToSuggest" />
					<text v-show="searchType != 1" class="iconfont icon-guanbi" @tap="handleToClose"></text>
				</view>
				<!-- 搜索榜 -->
				<template v-if="searchType == 1">
					<!-- 历史记录 -->
					<view class="search-history">
						<view class="search-history-head">
							<text>历史记录</text>
							<text class="iconfont icon-icon" @tap="handleToClear"></text>
						</view>
						<view class="search-history-list">
							<view v-for="(item, index) in searchHistory" :key="index" @tap="handleToWord(item)">
								{{item}}
							</view>
							<!-- #ifdef H5 -->
							<text v-show="searchHistory.length == 0">无历史记录</text>
							<!-- #endif -->
						</view>
					</view>

					<!-- 热搜榜 -->
					<view class="search-hot">
						<view class="search-hot-head">热搜榜</view>
						<view class="search-hot-item" v-for="(item, index) in searchHot" :key="index"
							@tap="handleToWord(item.searchWord)">
							<view class="search-hot-top">{{index + 1}}</view>
							<view class="search-hot-word">
								<view>
									{{item.searchWord}}
									<image :src="item.iconUrl" mode="aspectFit"></image>
								</view>
								<view v-show="!(item.content === '')">{{item.content}}</view>
							</view>
							<text class="search-hot-count">{{item.score}}</text>
						</view>
					</view>
				</template>

				<!-- 搜索结果 -->
				<template v-else-if="searchType == 2">
					<view class="search-result">
						<view class="search-result-item" v-for="(item, index) in searchList" :key="index"
							@tap="handleToDetail(item.id)">
							<view class="search-result-word">
								<view>{{item.name}}</view>
								<view>{{item.artists[0].name}} - {{item.album.name}}</view>
							</view>
							<text class="iconfont icon-bofang1"></text>
						</view>
					</view>
				</template>

				<!-- 搜索提示 -->
				<template v-else-if="searchType == 3">
					<view class="search-suggest">
						<view class="search-suggest-head">搜索“{{searchWord}}”</view>
						<view class="search-suggest-item" v-for="(item, index) in searchSuggest" :key="index"
							@tap="handleToWord(item.keyword)">
							<text class="iconfont icon-fangdajing"></text>
							{{item.keyword}}
						</view>
					</view>
				</template>
			</scroll-view>
		</view>
	</view>
</template>

<script>
	import '@/common/iconfont.css'
	import musichead from '../../components/musichead/musichead.vue'
	import {
		searchHot,
		searchWord,
		searchSuggest
	} from '../../common/api.js'
	export default {
		data() {
			return {
				searchHot: [],
				searchWord: '',
				searchHistory: [],
				searchType: 1,
				searchList: [],
				searchSuggest: [],
				// 状态栏高度
				statusBarHeight: 20,
				// 导航栏高度
				navBarHeight: 40
			}
		},
		onLoad() {

			// 获取手机系统信息
			const info = uni.getSystemInfoSync()
			// 设置状态栏高度
			this.statusBarHeight = info.statusBarHeight
			// #ifndef H5 || APP-PLUS || MP-ALIPAY
			// 获取胶囊的位置
			const menuButtonInfo = uni.getMenuButtonBoundingClientRect()
			// 导航栏高度 = (胶囊底部高度 - 状态栏的高度) + (胶囊顶部高度 - 状态栏的高度)
			this.navBarHeight = (menuButtonInfo.bottom - info.statusBarHeight) + (menuButtonInfo.top - info
				.statusBarHeight)
			// #endif

			searchHot().then((res) => {
				if (res[1].data.code == 200) {
					this.searchHot = res[1].data.data
				}
			})
			uni.getStorage({
				key: 'searchHistory',
				success: (res) => {
					this.searchHistory = res.data
				}
			})
		},
		methods: {
			handleToWord(word) {
				this.searchWord = word
				this.handleToSearch(word)
			},
			handleToSearch(word) {
				this.searchHistory.unshift(word)
				this.searchHistory = [...new Set(this.searchHistory)]
				if (this.searchHistory.length > 10) {
					this.searchHistory.length = 10
				}
				uni.setStorage({
					key: 'searchHistory',
					data: this.searchHistory
				})
				this.getSearchList(word)
			},
			handleToClear() {
				uni.removeStorage({
					key: 'searchHistory',
					success: (res) => {
						this.searchHistory = []
					}
				});
			},
			getSearchList(word) {
				searchWord(word).then((res) => {
					if (res[1].data.code == 200) {
						this.searchList = res[1].data.result.songs
						this.searchType = 2
					}
				})
			},
			handleToClose() {
				this.searchWord = ''
				this.searchType = 1
			},
			handleToDetail(songId) {
				uni.navigateTo({
					url: '/pages/detail/detail?songId=' + songId
				})
			},
			handleToSuggest(ev) {
				let value = ev.detail.value
				if (!value) {
					this.searchType = 1
					return
				}
				searchSuggest(value).then((res) => {
					if (res[1].data.code == 200) {
						this.searchSuggest = res[1].data.result.allMatch
						this.searchType = 3
					}
				})
			}
		}
	}
</script>

<style>
	.search-search {
		display: flex;
		align-items: center;
		height: 70rpx;
		margin: 10rpx 30rpx 30rpx 30rpx;
		background: #f7f7f7;
		border-radius: 50rpx;
	}

	.search-search text {
		margin: 0 26rpx;
	}

	.search-search input {
		flex: 1;
		font-size: 26rpx;
	}


	.search-history {
		margin: 0 30rpx 50rpx;
	}

	.search-history-head {
		display: flex;
		justify-content: space-between;
		margin-bottom: 36rpx;
	}

	.search-history-list {
		display: flex;
		flex-wrap: wrap;
		font-size: 22rpx;
	}

	.search-history-list view {
		padding: 16rpx 28rpx;
		border-radius: 40rpx;
		margin-right: 30rpx;
		margin-bottom: 30rpx;
		background: #f7f7f7;
	}

	.search-history-list text {
		padding: 16rpx 28rpx;
		margin-bottom: 30rpx;
		color: #ccc;
	}

	.search-hot {
		margin: 0 30rpx;
		overflow: hidden;
	}

	.search-hot-head {
		margin-bottom: 36rpx;
	}

	.search-hot-item {
		display: flex;
		align-items: center;
		margin-bottom: 58rpx;
	}

	.search-hot-top {
		color: #fb2222;
		width: 60rpx;
		margin-left: 8rpx;
	}

	.search-hot-word {
		flex: 1;
		width: 0;
	}

	.search-hot-word view:nth-child(1) {
		font-size: 30rpx;
		color: #000;
	}

	.search-hot-word view:nth-child(2) {
		font-size: 22rpx;
		color: #878787;
		margin-top: 8rpx;
		width: 92%;
		white-space: nowrap;
		text-overflow: ellipsis;
		overflow: hidden;
	}

	.search-hot-word image {
		width: 48rpx;
		height: 22rpx;
		margin-left: 8rpx;
	}

	.search-hot-count {
		color: #878787;
		font-size: 22rpx;
	}

	.search-result {
		border-top: 2rpx solid #e4e4e4;
		padding: 30rpx;
	}

	.search-result-item {
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding-bottom: 30rpx;
		margin-bottom: 30rpx;
		border-bottom: 2rpx solid #e4e4e4;
	}

	.search-result-word {}

	.search-result-word view:nth-child(1) {
		font-size: 28rpx;
		color: #235790;
		margin-bottom: 12rpx;
	}

	.search-result-word view:nth-child(2) {
		font-size: 22rpx;
		color: #898989;
	}

	.search-result-word text {
		font-size: 50rpx;
	}

	.search-suggest {
		border-top: 2rpx #e4e4e4 solid;
		padding: 30rpx;
		font-size: 26rpx;
	}

	.search-suggest-head {
		color: #4574a4;
		margin-bottom: 74rpx;
		font-size: 26rpx;
	}

	.search-suggest-item {
		color: #5d5d5d;
		margin-bottom: 74rpx;
	}

	.search-suggest-item text {
		color: #bdbdbd;
		margin-right: 28rpx;
		position: relative;
		top: 2rpx;
	}
</style>
